import { toast } from "react-toastify";
import server from "./server";

export const fetchCvs = async ({ callBack, ...payload }) => {
  try {
    const response = await server.post("/cv/search", payload);
    callBack();
    return response.data.data;
  } catch (error) {
    console.log(error);
    toast.error("حدث خطأ ما");
    return error;
  }
};

export const getAllSpecialty = async () => {
  const specialties = await server.get("/cv/specialties");
  return specialties.data.data;
};

export const getSubscritionTypes = async () => {
  const subTypes = await server.get("/cv/searchTypes");
  return subTypes.data.data;
};

export const getAllNationality = async () => {
  const countries = await server.get("/cv/countries");
  return countries.data.data;
};

export const getUsersCv = async () => {
  const userCvResponse = await server.get("/cv");
  return userCvResponse.data.data;
};

export const addCv = async (data) => {
  const addCvResponse = await server.post("/cv", data);
  return addCvResponse.data.data;
};

export const getCVSubscription = async (data) => {
  const getCVSubscriptionResponse = await server.post(
    "/cv/addSearchType",
    data
  );
  return getCVSubscriptionResponse.data.data;
};

export const searchCV = async (data) => {
  const searchCVResponse = await server.post("/cv/search", data);
  return searchCVResponse.data.data;
};
