import React from "react";
import { Col, Container, Row, Dropdown, DropdownButton } from "react-bootstrap";

// needed asssets
import appLogo from "assets/images/Logo.PNG";
import userIcon from "assets/images/userIcon.png";
import { ReactComponent as LungIcon } from "assets/svgs/lungicon.svg";
import { ReactComponent as SearchIcon } from "assets/svgs/searchIcon.svg";
import { ReactComponent as Hamburger } from "assets/svgs/hamburgermenu.svg";

// needed styles
import navBarStyles from "./index.module.scss";
import { Link, NavLink } from "react-router-dom";
import { FormattedMessage, useIntl } from "react-intl";
import { toggleSideNavBar } from "helpers/functions";
import { useDispatch, useSelector } from "react-redux";
import { signOutUser } from "store/auth/slice";

const NavBarComponent = ({ currentLocale, setCurrentLocale }) => {
  const intl = useIntl();
  const dir = localStorage.getItem("locale") === "ar" ? "rtl" : "ltr";
  const handleHamburgerMenuClick = () => {
    toggleSideNavBar();
  };

  const dispatch = useDispatch();
  const {
    isLoggedIn,
    user: { picture },
  } = useSelector((state) => state.authentication) || {};

  const handleSignOutUser = () => {
    dispatch(signOutUser());
  };

  const navLinks = [
    {
      name: ` ${intl.formatMessage({ id: "trips" })} `,
      link: "/trips",
    },
    {
      name: ` ${intl.formatMessage({ id: "lookForEmployee" })} `,
      link: "/searchforemployee",
    },
    {
      name: ` ${intl.formatMessage({ id: "documentation-of-papers" })} `,
      link: "/docmentrequest",
    },
  ];

  const renderNavItems = () => {
    return (
      <ul className={navBarStyles["nav-links-container"]}>
        {navLinks?.map((link) => (
          <NavLink to={`${link.link}`} className={navBarStyles["nav-link"]}>
            {link?.name}
          </NavLink>
        ))}
      </ul>
    );
  };

  return (
    <nav className={navBarStyles.navBarBody}>
      <Container>
        <Row>
          <Col
            xs={{ span: 12, order: 1 }}
            md={{ span: 12, order: 1 }}
            lg={{ span: 2, order: 1 }}
            className={navBarStyles.appLogoContainer}
          >
            <NavLink to={"/"}>
              <img
                className={navBarStyles.appLogo}
                src={appLogo}
                alt="app logo"
              />
            </NavLink>
          </Col>
          {/* <Col
            xs={{ span: 1, order: 5 }}
            md={{ span: 1, order: 5 }}
            lg={{ span: 2, order: 2 }}
          /> */}
          <Col
            className={navBarStyles.navCol}
            xs={{ span: 12, order: 5 }}
            md={{ span: 12, order: 5 }}
            lg={{ span: 5, order: 3 }}
          >
            {/* <div className={navBarStyles["search-box"]}>
              <input
                placeholder={intl.formatMessage({ id: "whereToGo" })}
                type="text"
                name=""
                id=""
              />
              <SearchIcon />
            </div> */}
            {renderNavItems()}
            <div></div>
          </Col>

          <Col
            className={`${navBarStyles.navCol} ${navBarStyles.deskTopDropDown}`}
            xs={{ span: 4, order: 3 }}
            md={{ span: 2, order: 3 }}
            lg={{ span: 1, order: 4 }}
          >
            <Dropdown
              dir
              id={`dropdown-button-drop-${dir === "rtl" ? "start" : "end"}`}
              className={`${navBarStyles.langDropDown} ${navBarStyles.dropDownBtn}`}
            >
              <Dropdown.Toggle id="dropdown-basic">
                <LungIcon />
              </Dropdown.Toggle>

              <Dropdown.Menu>
                <Dropdown.Item
                  onClick={() => {
                    setCurrentLocale("en");
                    localStorage.setItem("locale", "en");
                  }}
                >
                  <FormattedMessage id="en" />
                </Dropdown.Item>
                <Dropdown.Item
                  onClick={() => {
                    setCurrentLocale("ar");
                    localStorage.setItem("locale", "ar");
                  }}
                >
                  <FormattedMessage id="ar" />
                </Dropdown.Item>
              </Dropdown.Menu>
            </Dropdown>
          </Col>
          <Col
            className={`${navBarStyles.navCol} ${navBarStyles.deskTopDropDown}`}
            xs={{ span: 4, order: 3 }}
            md={{ span: 2, order: 3 }}
            lg={{ span: 1, order: 4 }}
          ></Col>

          <Col
            className={`${navBarStyles.navCol} ${navBarStyles.mobialDropDown}`}
            xs={{ span: 4, order: 3 }}
            md={{ span: 6, order: 3 }}
            lg={{ span: 1, order: 4 }}
          >
            <Dropdown className={navBarStyles.langDropDown}>
              <Dropdown.Toggle id="dropdown-basic">
                <LungIcon />
              </Dropdown.Toggle>

              <Dropdown.Menu>
                <DropdownButton
                  dir={dir}
                  title={intl.formatMessage({ id: "selectCurrency" })}
                  id={`dropdown-button-drop-${dir === "rtl" ? "start" : "end"}`}
                  className={navBarStyles.dropDownBtn}
                >
                  <Dropdown.Item
                  // TODO add currency to the store
                  // onClick={() => {
                  //     setCurrentLocale("en");
                  //     localStorage.setItem("locale", "en");
                  //   }}
                  >
                    USD
                  </Dropdown.Item>
                  <Dropdown.Item
                  // TODO add currency to the store
                  // onClick={() => {
                  //   setCurrentLocale("ar");
                  //   localStorage.setItem("locale", "ar");
                  // }}
                  >
                    EGP
                  </Dropdown.Item>
                </DropdownButton>
                <DropdownButton
                  dir
                  title={intl.formatMessage({ id: "selectLanguage" })}
                  id={`dropdown-button-drop-${dir === "rtl" ? "start" : "end"}`}
                  className={navBarStyles.dropDownBtn}
                >
                  <Dropdown.Item
                    onClick={() => {
                      setCurrentLocale("en");
                      localStorage.setItem("locale", "en");
                    }}
                  >
                    <FormattedMessage id="en" />
                  </Dropdown.Item>
                  <Dropdown.Item
                    onClick={() => {
                      setCurrentLocale("ar");
                      localStorage.setItem("locale", "ar");
                    }}
                  >
                    <FormattedMessage id="ar" />
                  </Dropdown.Item>
                </DropdownButton>
              </Dropdown.Menu>
            </Dropdown>
          </Col>

          <Col
            className={navBarStyles.navCol}
            xs={{ span: 6, order: 4 }}
            md={{ span: 5, order: 3 }}
            lg={{ span: 2, order: 5 }}
          >
            {isLoggedIn ? (
              <>
                <button onClick={handleSignOutUser}>
                  <Link to="/">
                    <p>
                      <FormattedMessage id="logout" />
                    </p>
                  </Link>
                </button>
                <button title={intl.formatMessage({ id: "viewProfile" })}>
                  <Link to={"/profile"}>
                    <img src={userIcon} alt="user Icon" />
                  </Link>
                </button>
              </>
            ) : (
              <button>
                <Link to="/login">
                  <p>
                    <FormattedMessage id="login" />
                  </p>
                  <img src={userIcon} alt="user Icon" />
                </Link>
              </button>
            )}
          </Col>

          <Col
            className={navBarStyles.navCol}
            xs={{ span: 1, order: 4 }}
            md={{ span: 1, order: 3 }}
            lg={{ span: 1, order: 5 }}
          >
            <div
              onClick={handleHamburgerMenuClick}
              className={navBarStyles["hamburger-menu"]}
            >
              <Hamburger />
            </div>
          </Col>
        </Row>
      </Container>
    </nav>
  );
};

export default NavBarComponent;
